.App{
    width: 100%;
    background-color: black;
    display: flex;
    justify-content: center;
    scroll-behavior: smooth;

    .workarea{
        width: 90%;

        .top{
            width: 100%;

            .top_image{
                overflow: hidden;
        
                img{
                    width: 100%;
                    object-fit: contain;
                }
            }

            .free_div{
                width: 90%;
                position: absolute;
                top: 16vw;
                display: flex;
                justify-content: center;

                .left{
                    flex: 6;
                    display: flex;
                    justify-content: start;

                    h1{
                        font-size: 3vw;
                        color: white;
            
                        span{
                            color: yellow;
                        }
                    }
                }

                .right{
                    flex: 6;
                    display: flex;
                    justify-content: end;

                    .icons{
                        display: flex;
                        flex-direction: column;
                        justify-content: space-evenly;

                        .icon{
                            color: white;
                            font-size: 3vw;
                        }
                    }
                }
            }
        }

        .middle{
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;

            h1{
                color: white;
                font-size: 4vw;
                margin-top: 10vw;
                margin-bottom: 0;

                span{
                    color: yellow;
                }
            }

            .perk1{
                display: flex;
                justify-content: center;
                padding: 5vw 5vw 0 5vw;

                .left{
                    flex: 6;
                    overflow: hidden;
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    img{
                        width: 70%;
                        object-fit: contain;
                    }
                }

                .right{
                    flex: 6;                    
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                                        
                    h1{
                        color: white;
                        font-size: 2.5vw;
                        margin-top: 0;
                        margin-right: 7vw;
                    }

                    h3,p{
                        color: white;
                        margin-right: 7vw;
                    }
                }
            }

            .perk2{
                display: flex;
                justify-content: center;
                padding: 5vw 5vw 0 5vw;

                .left{
                    flex: 6;                    
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                                        
                    h1{
                        color: white;
                        font-size: 2.5vw;
                        margin-top: 0;
                        margin-left: 7vw;
                    }

                    h3,p{
                        color: white;
                        margin-left: 7vw;
                    }                    
                }

                .right{                
                    flex: 6;
                    overflow: hidden;
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    img{
                        width: 70%;
                        object-fit: contain;
                    }
                }
            }

            .perk3{
                display: flex;
                justify-content: center;
                padding: 5vw;

                .left{
                    flex: 6;
                    overflow: hidden;
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    img{
                        width: 70%;
                        object-fit: contain;
                    }
                }

                .right{
                    flex: 6;                    
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                                        
                    h1{
                        color: white;
                        font-size: 2.5vw;
                        margin-top: 0;
                        margin-right: 7vw;
                    }

                    h3,p{
                        color: white;
                        margin-right: 7vw;
                    }
                }
            }

        }

        .bottom{
            display: flex;
            justify-content: center;
            align-items: center;
            margin-bottom: 5vh;

            .left{
                flex: 6;
                
                h3{
                    color: white;
                }
            }

            .right{
                flex: 6;
                object-fit: contain;
                height: 300px;
                border-radius: 10% 0 10% 0;
                overflow: hidden;
                padding: 5vw;
            }
        }
    }    
}